@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;700&family=Sora&display=swap);
/* Import Roboto Mono and Sora fonts from Google*/
@font-face {
  font-family: "Blair";
  src: url(/scanx-okur-chung/build/static/media/blair.868a6000.ttf);
}

.MuiTableCell-root {
  padding: 8px 12px !important;
  text-align: left !important;
}

label, input, span {
  font-family: "IBM Plex Mono" !important;
}

body {
  margin: 0;
  font-family: "IBM Plex Mono";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0A0A0A;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

